*, :before, :after {
  box-sizing: border-box;
}

#browser-css-not-supported {
  display: none;
}

@supports not ((display: flex) and (transform: translate(50%, 50%))) {
  #stage {
    display: none !important;
  }

  #browser-css-not-supported {
    display: block !important;
  }
}

@media screen {
  .for-print-only {
    display: none;
  }
}

@font-face {
  font-family: runes;
  src: url("BabelStoneRunicElderFuthark.26f2b7eb.woff") format("woff");
}

:root {
  --primary-base: #4f381b;
  --primary-contrast: #fff;
  --primary-highlight: #352c22;
  --secondary-base: #262673;
  --secondary-contrast: #fff;
  --secondary-highlight: #339;
  --tertiary-base: #732626;
  --tertiary-contrast: #fff;
  --tertiary-highlight: #933;
  --background-base: #f2f2f2;
  --background-contrast: #000;
  --background-highlight: #d9d9d9;
  --error-base: #862d2d;
  --error-contrast: #fff;
  --error-highlight: #c66;
  --window-base: #000;
  --window-contrast: #fff;
  --window-highlight: #e6e6e6;
  --splash-color: #81795d;
  --padding-thickness: .25rem;
  --font-base-size: 15px;
  --line-thickness: max(2px, .2rem);
  --outline-thickness: 2px;
  --outline-offset: calc(.5 * var(--outline-thickness));
  --panel-border: solid var(--line-thickness);
  --margin-thickness: calc(var(--outline-offset)  + var(--line-thickness));
  --vh: 1vh;
}

body {
  background-color: var(--window-base);
  color: var(--window-contrast);
  font-family: Fondamento, cursive;
  font-style: normal;
  font-weight: 400;
  font-size: calc(var(--font-base-size)  + .00390625 * var(--design-width));
  height: calc(var(--vh, 1vh) * 100);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

a:link, a:visited, a:active {
  color: var(--primary-contrast);
}

.runes, .rune-puzzle button {
  font-family: runes;
}

.runes {
  text-align: center;
}

#game-canvas {
  position: absolute;
}

#game-content {
  overflow: hidden;
}

.glass {
  background-color: var(--window-base);
  opacity: 0;
  height: calc(var(--vh, 1vh) * 100);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100vw;
  height: 100svh;
  transition: opacity .5s;
  position: absolute;
}

.glass-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
  overflow: auto;
}

.glass-content.overflow {
  justify-content: flex-start;
}

.glass.door {
  background-image: url("dungeon-splash.395230bd.jpg");
}

.glass.wall {
  background-image: url("wall.ac788cbf.jpg");
}

.glass.mask {
  background-color: #0006;
}

.glass-content > div {
  background-color: var(--window-base);
  border: 2px solid var(--window-contrast);
  color: var(--window-contrast);
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  min-width: 70%;
  max-width: 99%;
  margin: 4px;
  padding: .5rem;
  display: flex;
}

.glass-content .action-buttons-col {
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  display: flex;
}

.glass-content .action-buttons-row {
  flex-direction: row;
  justify-content: center;
  gap: 4px;
  display: flex;
}

.dialog-scroll-content {
  max-height: calc(var(--vh, 1vh) * 80);
  max-height: 80svh;
  overflow-y: auto;
}

.dialog-title {
  font-weight: bold;
}

.action-buttons-row button {
  flex: 1;
}

.side-by-side {
  flex-direction: row;
  gap: 3px;
  display: flex;
}

.side-by-side > div {
  flex: 1;
}

.side-by-side > div:nth-child(odd) {
  border-right: solid var(--primary-highlight) 1px;
  padding-right: 3px;
}

.store-contents {
  flex-flow: wrap;
  gap: 4px;
  display: flex;
}

.store-contents button {
  font-size: .85rem;
}

.stores p {
  text-align: center;
}

.actor-id-card span {
  margin-left: 1rem;
  display: inline-block;
}

.actor-id-card span:nth-child(2) {
  margin-left: 4px;
}

.actor-id-card canvas {
  border: solid 2px var(--window-contrast);
  background-color: #646464;
}

.store-contents button canvas {
  background-color: #646463;
  border: 2px solid #fff;
  border-radius: 50%;
  margin-right: 4px;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.2rem;
}

h3 {
  font-size: 1.1rem;
}

label {
  background-image: url("button-texture.ae62a420.jpg");
  justify-content: space-between;
  padding-left: 4px;
  padding-right: 4px;
  line-height: 48px;
  display: flex;
}

button {
  background-color: var(--primary-base);
  color: var(--primary-contrast);
  text-align: center;
  background-image: url("button-texture.ae62a420.jpg");
  font-family: Fondamento, cursive;
  font-size: 1rem;
}

button:hover {
  cursor: pointer;
}

button.uncastable {
  text-decoration: line-through;
}

.glass-content label:nth-of-type(2n) {
  background-color: #ffffff1a;
}

.icon-button {
  text-align: left;
  flex-direction: row;
  place-content: center space-between;
  align-items: center;
  display: flex;
}

.close-button {
  color: #fff;
  text-align: center;
  background-color: #000;
  padding: 1px;
  position: absolute;
  top: 0;
  right: 0;
}

.close-button:before {
  content: "X";
}

.popup {
  background-color: var(--window-base);
  opacity: 0;
  height: calc(var(--vh, 1vh) * 100);
  background-image: url("dungeon-splash.395230bd.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100vw;
  height: 100svh;
  transition: opacity .5s;
  position: absolute;
}

.popup > div {
  color: var(--splash-color);
  top: 50vh;
  left: 0;
  top: calc(var(--vh, 1vh) * 50);
  text-align: center;
  background-color: #000c;
  border: none;
  width: 100vw;
  padding-top: 2rem;
  padding-bottom: 2rem;
  transition: opacity 1s;
  position: absolute;
  top: 50svh;
  transform: translate(0%, -50%);
}

.popup img {
  max-width: 90vw;
}

.progress-indicator {
  border: solid 2px var(--splash-color);
  color: var(--splash-color);
  width: 1rem;
  height: 1rem;
  animation-name: progress;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: relative;
}

@keyframes progress {
  from {
    left: 25vw;
  }

  to {
    left: 75vw;
  }
}

.styled-checkbox {
  line-height: 0;
  display: inline-block;
}

.styled-checkbox input[type="checkbox"] {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

.styled-checkbox input[type="checkbox"] ~ canvas.checked, .styled-checkbox input[type="checkbox"]:checked ~ canvas.unchecked {
  display: none;
}

.styled-checkbox input[type="checkbox"]:checked ~ canvas.checked {
